function handleClick(item: { Instrument: string;}) {
  console.log(item, 'click');
  if (item.Instrument === 'Unchained') {
    activeName.value = 'first';
  }
  if (item.Instrument === 'Robot1') {
    activeName.value = 'second';
  }
  if (item.Instrument === 'Cytation1') {
